import {
  CardContent,
  Container,
  Grid,
  Skeleton,
  Typography,
  Modal,
  Box,
  Card,
  CardHeader,
  Avatar,
  CardMedia,
  CardActions,
  IconButton,
  AppBar,
  Toolbar,
  Stack,
  Button,
} from "@mui/material";
import React, { useEffect } from "react";
import MainTitle from "../Texts/MainTitle";

import { borderRadius, styled } from "@mui/system";

import CancelIcon from "@mui/icons-material/Cancel";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import RemoveCircleIcon from "@mui/icons-material/RemoveCircle";

import json2mq from "json2mq";
import { useMediaQuery } from "@mui/material";

import { MenuData } from "./MenuData";
import "./ThirdSection.css";

import { useState } from "react";

//Styling components

const CloseButtonStyled = styled(CancelIcon)(({ theme }) => ({
  ...theme.typography,
  fontSize: "2.5rem",
  color: "white",
  marginRight: "-0.5em",
}));

//Styles MODAL

const styleModalPc = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "50%",
  outline: "none",
};

const styleModalMobile = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "90%",
  outline: "none",
};

const ControlsQuantity = (props) => {
  const [amount, setAmount] = useState(1);
  const [price, setPrice] = useState()

  const reduceAmount = () => {
    return amount <= 1 ? 0 : setAmount(amount - 1);
  };

  const addAmount = () => {
    setAmount(amount + 1);
  };


  return (
    <>
      <Grid container>
        <Grid
          item
          xs={12}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Stack direction={{ xs: "row" }}>
            <IconButton onClick={() => reduceAmount()}>
              <RemoveCircleIcon
                sx={{
                  fontSize: "2em",
                  color: "#D6D6D6",
                }}
              />
            </IconButton>
            <Typography
              sx={{
                lineHeight: "4em",
                paddingRight: "1em",
                paddingLeft: "1em",
                fontWeight: "lighter",
              }}
            >
              {amount}
            </Typography>
            <IconButton onClick={() => addAmount()}>
              <AddCircleIcon
                sx={{
                  fontSize: "2em",
                  color: "#D6D6D6",
                }}
              />
            </IconButton>
          </Stack>
        </Grid>
        <Grid
          item
          xs={12}
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Button
            variant="contained"
            sx={{
              borderRadius: "0",
              width: "100%",
              height: "4em",
              bgcolor: "var(--navbar-background)",
              '&:hover': {
                bgcolor:'#7ECD28'
              }
            }}
          >
            Agregar {amount} al carrito{props.price}
          </Button>
        </Grid>
      </Grid>
    </>
  );
};

const MyDishes = () => {
  const [loading, setLoading] = useState(true);
  const [openModal, setOpenModal] = useState(false);
  const [dataMenu, setDataMenu] = useState({});

  const matches = useMediaQuery(
    json2mq({
      minWidth: 1200,
    })
  );

  const handleOpen = (data) => {
    setOpenModal(true);
    setDataMenu(data);
  };

  const handleClose = () => setOpenModal(false);

  useEffect(() => {
    let timeOut = setTimeout(() => {
      setLoading(false);
    }, 4000);
    return () => {
      clearTimeout(timeOut);
    };
  }, []);

  return (
    <Grid
      container
      rowSpacing={5}
      columnSpacing={3}
      alignItems="center"
      justifyContent="center"
      sx={{
        marginBottom: "4em",
      }}
    >
      <Modal
        open={openModal}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        {matches === true ? (
          <Box sx={styleModalPc}>
            <Card sx={{ borderRadius: 0 }}>
              <AppBar
                sx={{ bgcolor: "var(--navbar-background)" }}
                position="fixed"
              >
                <Toolbar>
                  <Typography
                    sx={{ flexGrow: 1, fontWeight: "light", fontSize: "2rem" }}
                  >
                    {dataMenu.title}
                  </Typography>
                  <IconButton onClick={handleClose}>
                    <CloseButtonStyled
                      price={dataMenu.price}
                    />
                  </IconButton>
                </Toolbar>
              </AppBar>
              <CardMedia
                component="img"
                height="300"
                image={dataMenu.img}
                alt={dataMenu.title}
                sx={{ marginTop: "4em" }}
              />
              <CardContent>
                <Typography
                  variant="h4"
                  sx={{ color: "black", fontSize: "2.5rem" }}
                >
                  {dataMenu.title}
                </Typography>
                <Typography sx={{ color: "#545454" }}>
                  {dataMenu.description}
                </Typography>
              </CardContent>
              <CardActions sx={{ borderTop: "1px solid #d1d1d1" }}>
                <ControlsQuantity />
              </CardActions>
            </Card>
          </Box>
        ) : (
          <Box className="myScrollMenu" sx={styleModalMobile}>
            <Card sx={{ borderRadius: 0 }}>
              <AppBar
                sx={{ bgcolor: "var(--navbar-background)" }}
                position="fixed"
              >
                <Toolbar>
                  <Typography
                    sx={{ flexGrow: 1, fontWeight: "light", fontSize: "2rem" }}
                  >
                    {dataMenu.title}
                  </Typography>
                  <IconButton onClick={handleClose}>
                    <CloseButtonStyled />
                  </IconButton>
                </Toolbar>
              </AppBar>
              <CardMedia
                component="img"
                height="300"
                image={dataMenu.img}
                alt={dataMenu.title}
                sx={{ marginTop: "4em" }}
              />
              <CardContent sx={{ marginBottom: "2em" }}>
                <Typography
                  variant="h4"
                  sx={{ color: "black", fontSize: "2.5rem" }}
                >
                  {dataMenu.title}
                </Typography>
                <Typography sx={{ color: "#545454" }}>
                  {dataMenu.description}
                </Typography>
              </CardContent>
              <CardActions sx={{ borderTop: "1px solid #d1d1d1" }}>
                <ControlsQuantity 
                price={dataMenu.price}
                />
              </CardActions>
            </Card>
          </Box>
        )}
      </Modal>
      {MenuData.map((data) => {
        return (
          <React.StrictMode key={data.id}>
            <Grid item xs={12} md={4}>
              <Card
                onClick={() => handleOpen(data)}
                sx={{
                  maxWidth: "100%",
                  background: "#1E1E1E",
                  cursor: "pointer",
                }}
              >
                <CardHeader
                  sx={{ color: "white" }}
                  avatar={
                    <Avatar
                      sx={{
                        background: "var(--primary)",
                        color: "var(--navbar-background)",
                        fontWeight: "bolder",
                      }}
                      aria-label="recipe"
                    >
                      {data.title.substring(0, 2).toUpperCase()}
                    </Avatar>
                  }
                  title={data.title}
                  subheader={
                    <Typography variant="caption" sx={{ color: "#BABABA" }}>
                      {data.date}
                    </Typography>
                  }
                />

                {loading ? (
                  <Skeleton
                    animation="wave"
                    variant="rectangular"
                    width="100%"
                    height={200}
                    sx={{ background: "#333333" }}
                  />
                ) : (
                  <CardMedia
                    component="img"
                    height="194"
                    src={data.img}
                    alt={data.title}
                  />
                )}
                <CardContent>
                  <Typography variant="body2" color="#BABABA">
                    {data.description}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </React.StrictMode>
        );
      })}
    </Grid>
  );
};

const ThirdSection = () => {
  return (
    <div className="third-section">
      <Container>
        <MainTitle
          textContent="Menu"
          description="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent eget suscipit diam. Suspendisse iaculis tellus consequat neque feugiat, nec efficitur sem tempus. Morbi rhoncus erat nisl. Nunc fermentum enim nunc, a volutpat orci sollicitudin lacinia. Morbi porta dignissim est quis."
        />
        <MyDishes />
      </Container>
    </div>
  );
};

export default ThirdSection;
